/* ViewLists.css */

body {
  font-family: "Arial", sans-serif;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.header {
  background: transparent;
  color: #fff;
  text-align: center;
  padding: 1em 10px; /* Add padding to prevent overlap */
  font-size: 1.5em;
  position: relative;
  top: 120px;
  left: 50%;
  transform: translateX(-50%);
  max-width: 90%; /* Limit the width to prevent overlap on smaller screens */
}

.days {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 100px;
  margin-left: auto;
}

.day {
  width: 100%;
  max-width: 300px;
  margin: 0 auto 20px;
  padding: 20px;
  border: 1px solid #ddd;
  text-align: center;
}

.day h3 {
  border-bottom: 1px solid #ddd;
}

.day ul {
  margin-top: 10px;
  list-style: none;
}

/* Add media queries for responsiveness */

@media (min-width: 1200px) {
  .days {
    flex-direction: row;
    justify-content: center;
  }

  .day {
    margin-right: 20px;
  }

  .header {
    font-size: 1.2em; /* Adjust font size for better responsiveness */
  }
}

/* Additional media query for smaller screens */
@media (max-width: 480px) {
  .header {
    font-size: 1em; /* Further reduce font size for smaller screens */
  }
}
